<template>
	<div>
		 <headerA></headerA>
			<div class="top2"><img src="/static/images/top-bg2.png"></div>
			<div class="Middle6">
			<ul v-for="obj in listSellArr">
				<router-link :to="obj.href" v-if="!!obj.tubiao1">{{obj.tubiao1}}</router-link>
				<router-link :to="obj.href" v-if="!!obj.tubiao2">{{obj.tubiao2}}</router-link>
				<router-link :to="obj.href" v-if="!!obj.tubiao3">{{obj.tubiao3}}</router-link>
			</ul>
			</div>
		 <footerA></footerA>
	</div>
</template>

<script>
	import headerA from "./common/top"; 
	import footerA from "./common/bottom"; 

	export default{
		data(){
			return{
				listSellArr:[
				]
			}
		},
        components:{
  			headerA,
  			footerA

        },
                mounted(){//等节点转载完毕触发
			let _this=this;
			_this.$http({
				method:"get",//请求方式
				url:"./static/json/tubiaoneiye2.json",
				params:{}//发送的数据；get请求用属性params，post请求用属性body
			})
			.then(function(data){//cuccess;data后台返回的数据
					_this.listSellArr=data.body;
			},
			function(err){

				}
			)
		}
    }
</script>

<style lang="scss">
	.top2{width:100%;height:1.83rem;}
	.top2 img{width:100%;height:100%;}
	.Middle6{margin:0.48rem auto 1rem;width:5.76rem;height:auto;padding-bottom:4.5rem;}
	.Middle6 a{color:#3c3535;display:block;width:5.74rem;height:0.86rem;margin-bottom:0.03rem;background:#fff;border:1px solid #d4d4d4;border-radius:0.06rem;text-align: center;line-height:0.86rem;font-size:0.3rem;}
   	.Middle6 a:hover{color:#fff;width:5.76rem;height:0.86rem;margin-bottom:0.03rem;border-radius:0.06rem;text-align: center;line-height:0.86rem;font-size:0.3rem; background: url(../assets/images/libg.png)no-repeat;background-size: 100% 100%;}	 
</style>


