<template>
	<div>
		 <headerA></headerA>
		<div class="top2"><img src="/static/images/top-bg2.png"></div>
		<div v-for="obj in listSellArr" v-if="!!obj.listArr1">
			<h1 class="MBAh1">{{obj.biaoti}}</h1>
			<ul class="MBAul"  v-for="EDG in obj.listArr1">
				<router-link :to="EDG.href" v-if="!!EDG.neirong">{{EDG.neirong}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.neirong1">{{EDG.neirong1}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.neirong2">{{EDG.neirong2}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.neirong3">{{EDG.neirong3}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.neirong4">{{EDG.neirong4}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.neirong5">{{EDG.neirong5}}</router-link>
			</ul>
		</div>
		<div v-for="obj in listSellArr" v-if="!!obj.listArr2">
			<h1 class="MBAh1">{{obj.biaoti}}</h1>
			<ul class="MBAul"  v-for="EDG in obj.listArr2">
				<router-link :to="EDG.href" v-if="!!EDG.neirong">{{EDG.neirong}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.neirong1">{{EDG.neirong1}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.neirong2">{{EDG.neirong2}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.neirong3">{{EDG.neirong3}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.neirong4">{{EDG.neirong4}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.neirong5">{{EDG.neirong5}}</router-link>
			</ul>
		</div>
		 <footerA></footerA>
	</div>
</template>

<script>
	import headerA from "./common/top"; 
	import footerA from "./common/bottom"; 

	export default{
		data(){
			return{
				listSellArr:[
				]
			}
		},
        components:{
  			headerA,
  			footerA

        },
        mounted(){//等节点转载完毕触发
			let _this=this;
			_this.$http({
				method:"get",//请求方式
				url:"./static/json/neiyexinwen.json?2",
				params:{}//发送的数据；get请求用属性params，post请求用属性body
			})
			.then(function(data){//cuccess;data后台返回的数据
					_this.listSellArr=data.body;
			},
			function(err){

				}
			)
		}
    }
</script>

<style lang="scss">
	.MBAh1{width:100%;height:0.71rem;background: url(../assets/images/MBAbg.png)no-repeat;
		background-size:100% 100%;font-weight:400;font-size:0.3rem;color:#454545;line-height:0.71rem;padding-left:0.6rem;}
	.MBAul{width:100%;height:auto;}
	.MBAul a{width:100%;height:0.66rem;display:block;color:#3c3535;padding-left:0.6rem;border-bottom:1px solid #d4d4d4;line-height:0.66rem;}
</style>


