<template>
	
	<div>
	<headerA></headerA>
	<neiye :listArr="listSellArr"></neiye>	
	<footerA></footerA>	
	</div>
	
</template>

<script>
	import headerA from "./top"; 
	import neiye from "./neiye"; 
	import footerA from "./bottom"; 
	export default{

		data(){
			return {
				listSellArr:[
				]
			}
		},
        components:{
  		headerA,
  		neiye,
  		footerA
        },
        mounted(){//等节点转载完毕触发
			let _this=this;
			_this.$http({
				method:"get",//请求方式
				url:"./static/json/neiye.json",
				params:{}//发送的数据；get请求用属性params，post请求用属性body
			})
			.then(function(data){//cuccess;data后台返回的数据
					_this.listSellArr=data.body;
			},
			function(err){

				}
			)
		}
    }
</script>

<style lang="scss">
	
</style>


