<template>
	<div>
  <div class="left":class="{leftIn:showHideBool,leftOut:(!showHideBool && loadHideBool)}">
    <span><router-link to="/"><img src="../../assets/images/ce.png"> 网站首页</router-link></span>
    <span><router-link to="/tubiao"><img src="../../assets/images/ce1.png"> 瑞德品牌</router-link></span>
    <span><router-link to="/tubiao1"><img src="../../assets/images/ce2.png"> 课程介绍</router-link></span>
    <span><router-link to="/tubiao2"><img src="../../assets/images/ce3.png"> 活动资讯</router-link></span>
    <span><router-link to="/tubiao3"><img src="../../assets/images/ce4.png"> 父母MBA</router-link></span>
    <span><router-link to="/tubiao4"><img src="../../assets/images/ce5.png"> 新闻中心</router-link></span>
    <span><router-link to="/tubiao5"><img src="../../assets/images/ce6.png"> 预约试听</router-link></span>
  </div>
  <div class="bu"v-if="showHideBool"@click="showHideBool=false;loadHideBool=true"></div>
	<section class="header">
    <div class="logo"@click="showHideBool=true;loadHideBool=false"><span><img src="../../assets/images/logo.png"/></span></div>
    <div class="toubu"><span><img src="../../assets/images/toubu.png"/></span></div>
    <div class="logo1"><span><img src="../../assets/images/logo1.png"/></span></div>
  </section>
	</div>
</template>

<script>
	export default{
    data(){
      return{
        showHideBool:false,
        loadHideBool:false
      }
    },
    }
</script>

<style lang="scss">
.bu{position:fixed;width:100%;height:100%;left:0;top:0;z-index:8;}
 .leftIn{animation:LeftIn 0.5s forwards;}
 @keyframes LeftIn
 {
  0%{left:-100%;}
  100%{left:0;}
 }
 .leftOut{animation:LeftOut 0.5s forwards;}
 @keyframes LeftOut
 {
  0%{left:0;}
  100%{left:-100;}
 }

  .left{width:2.84rem;height:100%;background:#3c3535;position:fixed;left:-100%;top:0;z-index:9;}
  .left span{width:100%;height:0.97rem;display:block;border-bottom:1px solid #1e1818;line-height:0.6rem;}
  .left img{width:0.53rem;height:0.53rem;}
  .left a{display:block;width:100%;height:100%;color:#fff;padding:0.26rem 0 0 0.2rem;}
  .header {
  width: 100%;
  height: 0.84rem;
  background: url(../../assets/images/headerBG.png) repeat-x;
  overflow: hidden;
  background-size: 100% 100% ;
  }
  .logo {
    float: left;
    width: 0.95rem;
    height: 0.84rem;
    border-right: 1px solid #221d1d ;
  }
  .logo span {
    display: block;
    width: 0.34rem;
    height: 0.25rem;
    margin: 0.33rem 0 0 0.3rem;
  }
  .logo span img {
    width: 100%;
    height: 100%;
  }
  .toubu {
    width: 4.53rem;
    height: 100%;
    float: left;
  }
  .toubu span {
    display: block;
    width: 3.76rem;
    height: 0.52rem;
    margin: 0.18rem 0 0 0.49rem;
  }
  .toubu span img {
    width: 100%;
    height: 100%;
  }
  .logo1 {
    float: right;
    width: 0.9rem;
    height: 0.84rem;
    border-left: 1px solid #221d1d ;
  }
  .logo1 span {
    display: block;
    width: 0.44rem;
    height: 0.44rem;
    margin: 0.19rem 0 0 0.25rem;
  }
  .logo1 span img {
    width: 100%;
    height: 100%;
  }
</style>


