<template>
	<div>
	<headerA></headerA>
		<h1 class="top5">最新活动</h1>
		<div class="huodong" v-for="obj in listSellArr">
			<div class="neirong-bg"></div>
			<ul class="huodong-wenben">
				<li>{{obj.time}}</li>
				<li>{{obj.duixiang}}</li>
				<li>{{obj.didian}}</li>
			</ul>
			<span class="baoming">{{obj.canjian}}</span>
		</div>
			
		
		<footerA></footerA>
	</div>
</template>

<script lang="babel">
	import headerA from "./common/top"; 
	import footerA from "./common/bottom"; 
	export default{
		data(){
			return{
				listSellArr:[
				]
			}
		},
        components:{
  			headerA,
  			footerA
        },
        mounted(){//等节点转载完毕触发
			let _this=this;
			_this.$http({
				method:"get",//请求方式
				url:"./static/json/huodongneirong.json",
				params:{}//发送的数据；get请求用属性params，post请求用属性body
			})
			.then(function(data){//cuccess;data后台返回的数据
					_this.listSellArr=data.body;
			},
			function(err){

				}
			)
		}
    }
</script>

<style lang="scss">
	.top5{width:100%;height:0.71rem;background: url(../assets/images/neiye-topbg.png)no-repeat;font-size:0.3rem;font-weight:400;line-height:0.71rem;color:#008dce;padding-left:0.6rem;
	background-size:100% 100%;
	}
	.huodong{width:5.9rem;height:5.13rem;margin:0.2rem auto 0.7rem;background:#fff;border-radius:7px;padding:0.09rem 0 0 0.06rem;}
	.neirong-bg{width:5.76rem;height:2.78rem;background: url(../assets/images/huodong.png)no-repeat;background-size:100% 100%;}
	.huodong-wenben{width:4.44rem;height:1.14rem;margin:0.04rem 0 0.22rem 0;padding-left:0.22rem;}
	.huodong-wenben li{width:100%;height:0.38rem;line-height:0.38rem;color:#837f7f;}
	.baoming{display:block;width:5.65rem;height:0.72rem;margin:0 auto;background:#1eb9f6;border-radius:4px;color:#fff;line-height:0.72rem;text-align:center;font-size:0.29rem;}
</style>


