<template>
<div >
	<headerA></headerA>
	<div>
		<section class="audition">
			<h2>课程试听预约</h2>
			<div class="auditionbg">
				<p>免费名额有限，现在就留下您的电话号码，我们的顾问会尽<br />
快联系您，并为您预约一个合适的时间。</p>
				<div class="information">
					<section>宝宝名字: <input type="text" value="" /></section>
					<section>宝宝年龄: 
						<select name="" id=""></select>
						<select name="" id=""></select>
						<select name="" id=""></select>
					</section>
					<section>联系电话:  <input type="text"/></section>
					<section>课程选择:  <select name="" id=""></select></section>
					<section>最近中心:  <select name="" id=""></select></section>
					<section>哪里知道:  <select name="" id=""></select></section>
					<div class="confirm">点此确认</div>
				</div>
			</div>
		</section>
		
		
	</div>
	<footerA></footerA>
	
</div>
	
</template>

<script lang="babel">
	import headerA from "./common/top"; 
	import footerA from "./common/bottom"; 
	export default{
		data(){
			return{
				news:[],
			}
		},
        components:{
      	headerA,
      	footerA
        },
        mounted(){//等加载完毕后
//      	console.log(this.$http);
        	let _this=this;
        	_this.$http({
        		method:"get",
        		url:"./static/json/news.json",
        		params:{},
        	})
        	.then(function(data){
				console.log(data);
				_this.news=data.body;
			},function(err){
        		alert("请求失败");
        	});
        	
        }

    }
</script>
<style  >
.audition{padding: 0.13rem 0 0.41rem 0.23rem;background: #f1f2f2;}
.audition h2{height: 1.03rem;padding: 0.35rem 0.24rem 0 0.32rem;background-color: #443c3c;position: relative;color: #fff;font-weight: 400;}
.audition img{width: 2.11rem;height: 2.02rem;position: absolute;position: absolute;right: -0.14rem;top: -0.1rem;}
.audition{padding: 0.14rem 0.14rem 0.38rem 0.14rem;background: #fff;}
.audition p{padding: 0.15rem 0 0.14rem 0.38rem;font-size: 0.18rem;line-height: 0.34rem;color: #666666;background: #f0f0f0;margin-top: 0.2rem;}
.information{padding: 0.23rem 0 0.35rem 0.34rem;}
.information section{margin-bottom: 0.13rem;}
.confirm{margin:1.13rem auto 2.63rem;width: 4.89rem;height: 0.64rem;background: #d3332b;color: #fff;text-align: center;line-height: 0.64rem;font-size: 0.24rem;}
</style>



