<template>
	<div>
		<headerA></headerA>
			<div class="top2"><img src="/static/images/top-bg2.png"></div>
		<div class="Middle4">
			<h1 class="top4">特色课程</h1>
			<div v-for="obj in listSellArr"  v-if="!!obj.listArr1" class="Middle5">
			<li v-for="EDG in obj.listArr1">
				<router-link :to="EDG.href" v-if="!!EDG.tubiao1">{{EDG.tubiao1}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.tubiao2">{{EDG.tubiao2}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.tubiao3">{{EDG.tubiao3}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.tubiao4">{{EDG.tubiao4}}</router-link>
			</li>
			</div>
			<h1 class="top4">启蒙基础课程</h1>
			<div v-for="obj in listSellArr"  v-if="!!obj.listArr2" class="Middle5">
			<li v-for="EDG in obj.listArr2">
				<router-link :to="EDG.href" v-if="!!EDG.tubiao5">{{EDG.tubiao5}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.tubiao6">{{EDG.tubiao6}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.tubiao7">{{EDG.tubiao7}}</router-link>
				<router-link :to="EDG.href" v-if="!!EDG.tubiao8">{{EDG.tubiao8}}</router-link>
			</li>
			</div>
		</div>
		 <footerA></footerA>
	</div>
</template>

<script>
	import headerA from "./common/top"; 
	import footerA from "./common/bottom"; 

	export default{
		data(){
			return{
				listSellArr:[
				]
			}
		},
        components:{
  			headerA,
  			footerA
        },
         mounted(){//等节点转载完毕触发
			let _this=this;
			_this.$http({
				method:"get",//请求方式
				url:"./static/json/tubiaoneiye.json",
				params:{}//发送的数据；get请求用属性params，post请求用属性body
			})
			.then(function(data){//cuccess;data后台返回的数据
					_this.listSellArr=data.body;
			},
			function(err){

				}
			)
		}
    }
</script>

<style lang="scss">
	.Middle4{padding-bottom:2rem;} 
	.top4{width:100%;height:0.71rem;background: url(../assets/images/neiye-topbg.png)no-repeat;font-size:0.3rem;font-weight:400;line-height:0.71rem;text-align:center;color:#008dce;
	background-size:100% 100%;
	}
	.Middle5{margin:0.48rem auto 1rem;width:5.76rem;height:auto;}
	.Middle5 li{width:5.74rem;height:0.86rem;margin-bottom:0.03rem;background:#fff;border:1px solid #d4d4d4;border-radius:0.06rem;text-align: center;line-height:0.86rem;font-size:0.3rem;}
	.Middle5 a{color:#3c3535;display:block;width:100%;height:100%;}
   	.Middle5 li:hover{width:5.76rem;height:0.86rem;margin-bottom:0.03rem;border-radius:0.06rem;text-align: center;line-height:0.86rem;font-size:0.3rem; background: url(../assets/images/libg.png) no-repeat;background-size: 100% 100%;}
   	.Middle5 a:hover{color:#fff;}
</style>


