<template>
	<div>
		 <headerA></headerA>
		 <tubiao :listArr="listSellArr"></tubiao>
		 <footerA></footerA>
	</div>
</template>

<script>
	import headerA from "./common/top"; 
	import tubiao from "./common/tubiao-yong"; 
	import footerA from "./common/bottom"; 

	export default{
		data(){
			return{
				listSellArr:[
				]
			}
		},
        components:{
  			headerA,
  			tubiao,
  			footerA

        },
        mounted(){//等节点转载完毕触发
			let _this=this;
			_this.$http({
				method:"get",//请求方式
				url:"./static/json/tubiaoJson.json?1",
				params:{}//发送的数据；get请求用属性params，post请求用属性body
			})
			.then(function(data){//cuccess;data后台返回的数据
					_this.listSellArr=data.body;
			},
			function(err){

				}
			)
		}
    }
</script>

<style>	 
</style>


