import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import index from '@/components/index'
import tubiao from '@/components/tubiao'
import tubiao1 from '@/components/tubiao1'
import tubiao2 from '@/components/tubiao2'
import tubiao3 from '@/components/tubiao3'
import tubiao4 from '@/components/tubiao4'
import tubiao5 from '@/components/tubiao5'
import neiye from '@/components/common/tubiaoneiye'
import huodong from '@/components/huodongneirong'
Vue.use(Router)
Vue.use(Resource)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/tubiao',
      name: 'tubiao',
      component: tubiao
    },
     {
      path: '/tubiaoneiye/:id/:jh',
      name: 'neiye',
      component: neiye
    },
    {
      path: '/huodongneirong/:id/:NR',
      name: 'huodong',
      component: huodong
    },
    {
      path: '/tubiao1',
      name: 'tubiao1',
      component: tubiao1
    },
    {
      path: '/tubiao2',
      name: 'tubiao2',
      component: tubiao2
    },
    {
      path: '/tubiao3',
      name: 'tubiao3',
      component: tubiao3
    },
    {
      path: '/tubiao4',
      name: 'tubiao4',
      component: tubiao4
    },
    {
      path: '/tubiao5',
      name: 'tubiao5',
      component: tubiao5
    },
  ]
})



